.header {
  padding-top: 7px;
  padding-bottom: 0;
  margin-left: -20px;
  margin-right: -20px;
  border-bottom: 1px solid @another-grey;
  padding-left: 20px;

  @media (min-width: @tablet-width) {
    padding-top: 20px;
    padding-bottom: 15px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    border-bottom: none;
    margin-left: 0;
    margin-right: 0;
    padding-left: 0;


    &::before {
      display: none;
    }
  }

  @media (min-width: @desktop-width) {
    padding-top: 53px;
    padding-bottom: 9px;
  }
}

.header__list {
  list-style: none;
  margin: 0;
  padding: 0;
  margin-left: -20px;
  margin-right: -20px;

  @media (min-width: @tablet-width) {
    display: flex;
    flex-direction: row;
    margin-left: 0;
    margin-right: 0;
  }
}

.header__list--index::before {
  @media (min-width: @desktop-width) {
    content: "";
    width: 50%;
    height: 694px;
    background-image: url("../img/green-cat-desktop.jpg");
    background-repeat: no-repeat;
    background-position: right;
    background-size: cover;
    position: absolute;
    background-color: rgba(104, 183, 56, 0.85);

    top: 0;
    left: 50%;
  }
}

.header__item {
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 500;
  font-style: normal;
  font-size: 20px;
  line-height: 20px;
  text-transform: uppercase;
  color: @basic-black;
  text-align: center;
  margin: 0;
  padding-top: 22px;
  padding-bottom: 23px;
  border-bottom: 1px solid #e6e6e6;

  @media (min-width: @tablet-width) {
    font-family: "Oswald", "Arial", sans-serif;
    font-weight: 400;
    font-style: normal;
    font-size: 18px;
    line-height: 24px;
    text-transform: uppercase;
    color: @basic-black;
    margin-right: 35px;
    border-top: none;
    padding-top: 22px;
    padding-bottom: 8px;
    border-bottom: none;

    &--active {
      border-bottom: 2px solid @basic-green;
    }
  }

  @media (min-width: @desktop-width) {
    font-size: 20px;
    line-height: 30px;
    position: relative;
    padding-top: 16px;
    padding-bottom: 6px;
  }
}

.header__item--active.header__item--index {
  @media (min-width: @desktop-width) {
    border-bottom: 2px solid @basic-white;
  }
}

.header__item:first-child {
  border-top: 1px solid #e6e6e6;

  @media (min-width: @tablet-width) {
    border-top: none;
  }

  @media (min-width: @desktop-width) {
    width: 68px;
    margin-right: 37px;
  }
}

.header__item:last-child {
  @media (min-width: @tablet-width) {
    margin-right: 0;
  }
}

.header__link {
  text-decoration: none;
  color: @basic-black;

  &:hover {
    color: rgba(0, 0, 0, 0.6);
  }

  &:active {
    color: rgba(0, 0, 0, 0.3);
  }

  @media (min-width: @desktop-width) {
    &--index {
      color: @basic-white;

      &:hover {
        color: rgba(255, 255, 255, 0.6);
      }

      &:active {
        color: rgba(255, 255, 255, 0.3);
      }
    }
  }
}

.header__toggle {
  position: absolute;
  width: 30px;
  height: 30px;
  background-color: @basic-white;
  cursor: pointer;
  border: none;
  right: 20px;
  top: 21px;
  outline: none;

  @media (min-width: @tablet-width) {
    display: none;
  }
}

.header__nav--open .header__toggle {
  &::after {
    content: "";
    position: absolute;
    width: 24.76px;
    height: 0;
    left: 3px;
    top: 13px;
    border: 1px solid @basic-black;
    background-color: @basic-black;
    transform: rotate(45deg);
  }

  &::before {
    content: "";
    position: absolute;
    width: 24.76px;
    height: 0;
    left: 3px;
    top: 13px;
    border: 1px solid @basic-black;
    background-color: @basic-black;
    transform: rotate(-45deg);
  }
}

.header__nav--closed .header__list {
  @media (max-width: @mobile-width-only) {
    display: none;
  }
}

.header__nav--closed .header__toggle::before {
  content: "";
  position: absolute;
  width: 24px;
  height: 1.5px;
  top: 2px;
  left: 6px;
  background-color: @basic-black;
  box-shadow: 0 8px 0 0 @basic-black, 0 16px 0 0 @basic-black;
}

.header__wrapper {
  display: flex;
  justify-content: space-between;
  padding-top: 14px;
  padding-bottom: 11px;

  @media (min-width: @desktop-width) {
    padding-top: 12px;
  }
}

.header__logo {
  margin-top: -10px;

  &:hover {
    opacity: 0.8;
  }

  &:active {
    opacity: 0.6;
  }
}

.header__nav--nojs .header__list {
  display: initial;
}

.header__nav--nojs .header__toggle {
  display: none;
}
